<template>
    <div id="issue">
        <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="">
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-col :span="11">
                <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
                </el-col>
                <el-col class="line" :span="2">-</el-col>
                <el-col :span="11">
                <el-time-picker placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
                </el-col>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="form.resource">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
                <!-- <el-input type="textarea" v-model="form.desc"></el-input> -->
                <mavon-editor v-model="content" ref="md" @imgAdd="$imgAdd" @change="change" @save="save" style="min-height: 500px"/>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit"></el-button>
                <el-button></el-button>
            </el-form-item>
            </el-form>
    </div>
</template>

<script>
    import { mavonEditor } from 'mavon-editor'
    import 'mavon-editor/dist/css/index.css'

    export default {
        data() {
            return {
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                },
                    content:'',
                    html:'',
                    name: '',
                }
            },
        components: {
            mavonEditor
        },
        methods: {
            onSubmit() {
                console.log('submit!');
            },
            // md
            $imgAdd(pos, $file){
                var formdata = new FormData();
                formdata.append('file', $file);
                this.$axios({
                    url: '/common/upload',
                    method: 'post',
                    data: formdata,
                    headers: { 'Content-Type': 'multipart/form-data' },
                }).then((url) => {
                    this.$refs.md.$img2Url(pos, url);
                })
            },
            change(value, render){
                // render  markdown 
                this.html = render;
            },
            save(value, render){
                // valuerenderhtml
                // valuerenderbase64
                console.log(value, render)
            }
        }
    }
</script>
<style lang="scss" scoped>
#issue{
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    .el-form{
        width: 90%;
    }
}
</style>